body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* FLEX */
.flex { 
  display: flex;}
.horizontal, .horizontal-only {
  flex-direction: row; }
.vertical {
  flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal {
    flex-direction: column; }
}
.flex-center {
  justify-content: center;
  align-items: center; }
.flex-right {
  justify-content: flex-end;
  align-items: center; }
.flex-1 {
  flex: 1; }
.flex-2 {
  flex: 2; }
.flex-3 {
  flex: 3; }
.flex-4 {
  flex: 4; }
.flex-5 {
  flex: 5; }


.container, .container-big {
  width: calc(100%-20px);
  padding: 0 10px;
  max-width: 1024px;
  margin: 0 auto;
}
.container-big {
  max-width: 1440px;
}

/* FORM */
.form-input {
  border: 1px solid #aaa;
  font-size: 0.9rem;
  padding: 10px 25px;
  border-radius: 10px;
}

.fa-star {
  color: rgb(237, 209, 0);
}

.fi-color {
  color: #FF0054;
}

/* Topbar */
.Topbar {
  /* background-color: #FF0054;
  padding: 5px 20px;
  font-size: 0.8rem;
  color: #eee; */
  background-color: #ffdae6;
  padding: 5px 20px;
  font-size: 0.8rem;
  color: #f60830;
}
.Topbar i {
  padding: 2px 5px;
}

/* Header */
.Header {
  padding: 40px 20px;
}
.Header .logo {
  max-width: 200px;
}
.Header > div > div {
  margin: 8px 0;
}

.form-input.search {
  width: 90%;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.btn {
  cursor: pointer;
}
.btn.btn-search{
  border-radius: 10px;
  background-color: #FF0054;
  border: 1px solid #FF0054;
  font-size: 1.2rem;
  color: #eee;
  padding: 7.2px 15px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: 0;
}

.minha-conta {
  color: #FF0054;
}
.minha-conta span {
  font-size: 0.8rem;
}
.minha-conta i {
  padding: 3px 5px;
}

.icon-cart-number {
  background-color: #FF0054;
  color: #eee;
  border-radius: 100px;
  width: 18px;
  padding: 2px;
  margin-left: -15px;
  margin-top: -10px;
}

/* CATEGORIAS */
.Categorias {
  /* background-color: #FF0054; */
  color: #eee;
  font-size: 0.95rem;
  padding: 10px;
  /* GRADIENT */
  background: #f0f9ff; /* Old browsers */
  background: -moz-linear-gradient(left, #f0f9ff 0%, #f0f9ff 0%, #ff3575 0%, #ff0054 26%, #ff0054 26%, #ff0054 44%, #ff0054 66%, #ea0003 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #f0f9ff 0%,#f0f9ff 0%,#ff3575 0%,#ff0054 26%,#ff0054 26%,#ff0054 44%,#ff0054 66%,#ea0003 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #f0f9ff 0%,#f0f9ff 0%,#ff3575 0%,#ff0054 26%,#ff0054 26%,#ff0054 44%,#ff0054 66%,#ea0003 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f0f9ff', endColorstr='#ea0003',GradientType=1 ); /* IE6-9 */
}
.Categorias .item {
  cursor: pointer;
}
@media screen and (max-width: 720px){
  .Categorias {
    overflow-x: scroll;
  }
  .Categorias .item {
    padding: 0px 10px;
    text-align: center;
  }
}

/* HEROSHOP */
.HeroShop {
  padding: 20px;
}
.HeroShop .root {
  padding: 10px;
  font-size: 0.8rem;
  color: #888;
}

.small-photos {
  border: 1px solid #eee;
  margin: 2px;
  cursor: pointer;
}

.item-data h2 {
  margin: 5px 0;
  color: #272727;
}

.item-data .cod {
  margin: 5px 0;
  color: #787878;
  font-size: 0.85rem;
}

.item-data .star {
  padding: 3px 0;
  font-size: 0.9rem;
}

.item-data .old-price {
  padding-top: 10px;
  font-size: 0.9rem;
  color: #777;
  text-decoration: line-through;
}
.item-data .price-vista {
  color: #166506;
  font-size: 2rem;
  font-weight: bold;
  padding:  0 0 15px 0;  
}
.item-data .price-vista .tag {
  color: #eee;
  background-color: rgb(173, 16, 16);
  font-size: 0.8rem;
  padding: 3px;
  margin: 5px 15px;
}
.item-data .credit-card-value {
  color: #474747;
  font-size: 0.9rem;
}
.item-data .credit-card-value span {
  color: #ff0000;
  font-weight: bold;
  font-size: 0.9rem;
}
.item-data .options {
  padding: 15px 0 0 0;
}
.item-data .options > span {
  margin: 15px 0;
}
.type-cor{
  width: 35px;
  height: 35px;
  border: 1px solid #aaa;
  border-radius: 3px;
}
.cor-azul {
  background-color: #181531;
}
.item-data .options div {
  margin: 5px 0;
  cursor: pointer;
}
.item-data .options div span {
  border-radius: 2px;
  border: 1px solid #d7d7d7;
  padding: 8px;
  margin: 5px;
}
.item-data .options div span:hover {
  background-color: #f2f2f2;
}
.item-data .options div span:first-child {
  margin-left: 0;
}

/* QTD */
.margin-10 { margin: 10px 0; }
.qtd input {
  margin: 10px 0;
  font-size: 1rem;
  border-radius: 5px;
  border: 0.5px solid #ccc;
  padding: 10px 10px;
  width: 20px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.qtd button {
  background-color: #222;
  border: 0;
  border-radius: 5px;
  color: #FEFEFE;
  font-size: 0.9rem;
  border-radius: 5px;
}
.qtd button:first-child {
  border-radius: 0;
  border-top-right-radius: 5px;
}
.qtd button:last-child {
  border-radius: 0;
  border-bottom-right-radius: 5px;
  border-top: 0.5px solid #dadada;
}
.whishlist a:hover {
  color: #a71550;
}
.titulo-frete {
  color: #474747;
}
.frete {
  color: #777;
}
/* NUMBER */
input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}

.buy-button {
  background-color: rgb(71, 148, 43);
  color: #fefefe;
  padding: 15px 25px;
  width: 220px;
  font-size: 1.2rem;
  font-weight: bold;
  border: 0;
  border-radius: 10px;
  margin: 15px 0;
  cursor: pointer;
}
.buy-button:hover {
  background-color: rgb(42, 104, 19);
}

.whishlist {
  font-size: 1rem;
  color: #999;
  cursor: pointer;
}
.hr {
  width: 90%;
  background-color: #aaa;
  height: 1px;
  margin: 30px 0;
}

.frete label {
  font-weight: bold;
}
.frete input {
  font-size: 1rem;
  padding: 5px 10px;
  margin: 0 0 0 10px;
  border-radius: 5px;
  border: 1px solid #cdcdcd;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
@media screen and (max-width: 720px){
  .frete input {
    width: 100px;
  }
}
.frete button {
  padding: 5px 15px;
  font-size: 1rem;
  color: #272727;
  background-color: transparent;
  border-radius: 5px;
  border: 0.5px solid #ccc;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  cursor: pointer;
}
.frete button:hover {
  background-color: rgb(221, 221, 221);
}

/* Descricao */

/* AVAIALICADOs */
.painel-estrela {
  margin: 10px 0;
  border: 0.5px solid rgb(206, 206, 206);
  background-color: rgb(250, 250, 250);
  padding: 10px;
  border-radius: 10px;
}
.fila-estrela  {
  padding: 3px 0;
}

.painel-avaliacao {
  margin: 10px;
  padding: 20px;
  border: 0.5px solid #ddd;
  border-radius: 10px;
}
.painel-avaliacao .detalhes {
  font-size: 0.8rem;
  color: #777;
  padding: 10px 0;
}

.caracteristicas{
  padding: 15px;
}
.caracteristica-linha > div {
  padding: 8px 5px;
  height: 30px;
}
.caracteristica-linha > div:nth-child(2n){
  background-color: #e1e1e1;
}
.caracteristica-linha > div:nth-child(2n+1){
  background-color: transparent;
}

.descricao {
  padding: 15px;
  line-height: 180%;
}

.Newsletter{ 
  margin: 50px 0 -1px 0;
  /* background-color: rgb(215, 0, 57); */
  padding: 20px 10px;
  color: #FAFAFA;
  /* GRADIENT */
  background: #f0f9ff; /* Old browsers */
  background: -moz-linear-gradient(left, #f0f9ff 0%, #f0f9ff 0%, #ff3575 0%, #ff0054 26%, #ff0054 26%, #ff0054 44%, #ff0054 66%, #ea0003 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #f0f9ff 0%,#f0f9ff 0%,#ff3575 0%,#ff0054 26%,#ff0054 26%,#ff0054 44%,#ff0054 66%,#ea0003 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #f0f9ff 0%,#f0f9ff 0%,#ff3575 0%,#ff0054 26%,#ff0054 26%,#ff0054 44%,#ff0054 66%,#ea0003 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f0f9ff', endColorstr='#ea0003',GradientType=1 ); /* IE6-9 */
}

.Newsletter input {
  padding: 10px 15px;
  font-size: 1rem;
  border: 0;
  border-radius: 10px;
  width: calc(100%-30px);
  margin: 10px 0;
}

.Newsletter button {
  border: 0;
  border-radius: 10px;
  color: #fafafa;
  background-color: #1b9104;
  font-size: 1.1rem;
  padding: 15px 25px;
  cursor: pointer;
}
.Newsletter button:hover {
  background-color: #146504;
}

.Sugestoes .image {
  height: 100%;
}
.Sugestoes .item {
  cursor: pointer;
  padding: 15px;
}

.Sugestoes .preco-vista {
  text-align: center;
}
.Sugestoes .preco-vista span {
  color: #146504;
  font-size: 1.2rem;
  font-weight: bold;
}
.Sugestoes .preco-parc {
  font-size: 0.9rem;
  color: #777;
}

/* INSTITUCIONAL */
.Institucional {
  background-color: #171717;
  color: #fafafa;
  padding: 50px 10px;

}
.Institucional i {
  color: #ff0000;
  padding: 10px 5px 5px 5px;
}

.Institucional .items span {
  padding: 5px;
  font-size: 0.9rem;
  cursor: pointer;
}
.Institucional .items span:hover {
  color: #ff0000;
}

/* FOOTEr */
.Footer {
  background-color: #272727;
  color: #FAFAFA;
  padding: 10px;
  font-size: 0.8rem;
}

@media screen and (max-width: 720px){
  .no-mobile {
    display: none;
  }
}